.register-page {
  background-color: $color-snow;
  background-image: url("../img/svg/bg-paw.svg"), url("../img/svg/bg-paw.svg");
  background-position: 13.3% 95.4%, 27.9% 88%;
  background-repeat: no-repeat, no-repeat;
  flex-grow: 1;


  &__header {
    @include retina {
      background: url("../img/content/pattern-bg-red@2x.png") repeat-x,
      $color-light-salmon;
      background-size: cover;
    }

    background: url("../img/content/pattern-bg-red.png") repeat-x,
    $color-light-salmon;
    background-size: cover;
  }

  &__img-wrap {
    position: relative;
    min-height: 518px;
    overflow: hidden;
    max-width: 1280px;
    margin: 0 auto;
  }

  &__img {
    position: absolute;
    right: -40px;
    bottom: -67px;
    height: auto;
    width: 830px;
  }

  &__content {
    background-color: $color-default-white;
    padding: 20px 20px 80px;
  }

  &__inner {
    width: 100%;
    max-width: 560px;
    margin: 0 auto;
  }

  &__title {
    font-family: $ff-source-code-pro;
    font-size: 48px;
    font-weight: 400;
    line-height: 60px;
    color: $color-accent-main;
    margin: 0 0 10px 0;
  }

  &__fields {
    margin-bottom: 30px;
  }

  &__field {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__btn {
    max-width: 380px;
    align-self: center;
    margin-bottom: 10px;
  }

  &__form {
    margin-bottom: 30px;

    form {
      display: flex;
      flex-direction: column;
    }
  }

  &__text-wrap {
    width: 100%;
    max-width: 340px;
    font-family: $ff-source-code-pro;
    font-size: 24px;
    font-weight: 400;
    line-height: 40px;
    text-align: center;
    margin: 0 auto;
    color: $color-accent-main;
  }

  &__link {
    font-family: inherit;
    font-size: 24px;
    font-weight: 500;
    line-height: inherit;
    display: inline-flex;
    text-decoration: underline;
    text-underline-offset: 4px;
    color: $color-persian-red;

    outline: none;

    @include hover-focus {
      color: $color-geraldine;
    }

    @include active {
      color: $color-brown;
    }
  }
}
