.map {
  background-color: $color-default-white;
  padding: 47px 0 60px;
  min-height: 793px;

  &__title {
    font-size: 48px;
    line-height: 60px;
    font-weight: 400;
    color: $color-default-black;
    margin: 0 0 38px;
  }

  &__wrapper {
    margin: 0 0 37px;
    height: 472px;
    border-radius: 10px;
    background-color: $color-snow;
  }

  &__addresses {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px 87px;
    list-style: none;
    padding: 0;
    margin: 0;

    @include vp-1279 {
      column-gap: 10px;
    }

    @include vp-1023 {
      grid-template-columns: 1fr 1fr;

      & .map__address:nth-child(3n+3) {
        margin: 0;
      }
    }
  }

  &__address {
    width: 273px;
  }

  &__address:nth-child(3n+3) {
    margin: 0 0 0 53px;
  }
}
