.filter-sort {
  background-color: $color-snow;
  padding-top: 44px;

  &__inner {
    display: flex;
    min-height: 96px;
    align-items: flex-start;
    justify-content: space-between;
    padding: 15px 47px;

    @include vp-1279 {
      flex-direction: column;
    }
  }

  &__filter-wrap {
    display: grid;
    grid-template-columns: 312px auto;
    gap: 20px;
    align-items: baseline;
    margin-right: 10px;
  }

  &__filter-title {
    font-family: $ff-source-code-pro;
    font-size: 24px;
    font-weight: 400;
    line-height: 30px;
    margin: 19px 0 0;
  }

  &__filter-btn {
    font-family: $ff-source-code-pro;
    font-size: 32px;
    font-weight: 400;
    line-height: 40px;
    border: none;
    outline: none;
    background-color: transparent;
    cursor: pointer;
    padding: 0 0 7px 0;


    svg {
      margin-left: 11px;
      transition: transform $trans-default;
    }

    &:hover ~ .filter-sort__filter-list,
    &:focus-visible ~ .filter-sort__filter-list {
      opacity: 1;
      pointer-events: all;
    }

    @include hover-focus {
      svg {
        transform: rotate(90deg);
      }
    }
  }

  &__filter-list {
    margin: 0;
    padding: 20px 24px 24px 24px;
    list-style: none;
    background-color: $color-default-white;
    min-width: 245px;
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 0;
    pointer-events: none;
    z-index: 2;

    position: absolute;
    left: 66%;
    transform: translateX(-50%);

    @include hover-focus {
      opacity: 1;
      pointer-events: all;
    }
  }

  &__filter-item {
    margin: 0 0 16px 0;
    width: 100%;
    display: flex;
    justify-content: center;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__filter {
    position: relative;

    &:hover {
      svg {
        transform: rotate(90deg);
      }
    }
  }

  &__sort-wrap {
    display: grid;
    grid-template-columns: 307px auto;
    gap: 20px;
  }

  &__sort-title {
    font-family: $ff-source-code-pro;
    font-size: 24px;
    font-weight: 400;
    line-height: 30px;
    margin: 18px 0 0;
  }

  &__sort-btn {
    border: none;
    border-radius: 25px;
    outline: none;
    cursor: pointer;
    background-color: $color-default-white;
    width: 63px;
    height: 58px;

    @include active {
      svg {
        color: $color-brown;
      }
    }

    svg {
      color: $color-light-salmon;
    }

    &--active {
      svg {
        color: $color-brown;
      }
    }

    &--desc {
      svg {
        transform: rotate(180deg);
      }
    }
  }

  &__sort-btns-wrap {
    display: grid;
    grid-template-columns: 63px 63px;
    gap: 19px;
  }

}
