.number-of-favourites {
  background-color: $color-default-white;
  padding: 67px 0 61px;
  background-image: url("../img/svg/bg-big-paw.svg"), url("../img/svg/bg-big-paw.svg");
  background-position:5% 195px, 88.5% -67px;
  background-repeat: no-repeat, no-repeat;
  background-size: 509px 489px, 476px 447px;

  &__wrapper {
    margin: 0 0 124px;
  }

  &__wrap-price {
    display: flex;
    justify-content: start;
    align-items: end;

    &:not(:last-child) {
      margin: 0 0 20px;
    }
  }

  &__cakes {
    font-weight: 400;
    font-size: 48px;
    line-height: 60px;
    margin: 0 0 73px;
    padding: 0 0 15px;
    border-bottom: 1px solid $color-tomato;
  }

  &__text {
    font-weight: 400;
    font-size: 32px;
    line-height: 40px;
    text-align: start;
    margin: 0 185px 0 0;
    padding: 0;
    letter-spacing: 0.1em;

    &:not(:last-child) {
      margin: 0 185px 0 0;
    }
  }

  &__price {
    font-weight: 400;
    font-size: 48px;
    line-height: 60px;
    text-align: start;
    margin: 0;
    padding: 0;
  }

  &__button {
    max-width: 240px;

    &:not(:last-child) {
      margin: 0 0 20px;
    }
  }
}
