.star-rating {
  display: flex;

  &__star {
    stroke: $color-kournikova;
    stroke-width: 2px;
    fill: none;

    &:not(:last-child) {
      margin-right: 16px;
    }

    &--active {
      fill: $color-kournikova;;
    }
  }

  &__count {
    font-size: 12px;
    line-height: 15px;
    align-self: flex-start;
    margin-top: -2px;
  }

  &--big {
    .star-rating__star {
      width: 40px;
      height: 40px;

      &:not(:last-child) {
        margin-right: 22px;
      }
    }
  }
}
