.footer {
  width: 100%;
  padding: 38px 0 43px;
  background-color: $color-snow;

  font-size: 24px;
  line-height: 40px;
  color: $color-accent-main;

  &__wrapper {
    display: flex;
    align-items: flex-start;
  }

  &__feedback {
    display: flex;
    flex-direction: column;

    margin: 0 21% 0 0;
    max-width: 288px;
  }

  &__feedback-email {
    color: inherit;
    transition: color $trans-default;

    @include hover-focus {
      outline: none;
      color: $color-geraldine;
    }

    @include active {
      color: $color-brown;
    }
  }

  &__list {
    margin: 20px 0 0;
    padding: 0;

    display: grid;
    grid-template-columns: repeat(2, auto);
    align-items: center;
    gap: 22px;

    list-style: none;
  }

  &__item {
    width: 50px;
    height: 50px;
  }

  &__social-link {
    display: inline-flex;

    svg {
      color: $color-accent-main;
    }

    @include hover-focus {
      outline: none;

      svg {
        color: $color-light-salmon;
        transition: color $trans-default;
      }
    }

    @include active {
      svg {
        color: $color-geraldine;
      }
    }
  }

  &__devolopers {
    margin: 0 5px 0 auto;
    max-width: 188px;

    color: $color-accent-main;

    transition: color $trans-default;

    @include hover-focus {
      outline: none;
      color: $color-geraldine;
    }

    @include active {
      color: $color-brown;
    }
  }
}
