.error-loading {
  background-color: $color-snow;
  background-image: url("../img/svg/bg-paw.svg"), url("../img/svg/bg-paw.svg"), url("../img/svg/bg-paw.svg"), url("../img/svg/bg-paw.svg");
  background-repeat: no-repeat, no-repeat;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 40px 0;
  background-position: 15% 92.5%, 29.5% 84%, 75% 14%, 89% 6.5%;

  &__title {
    font-weight: 400;
    font-size: 48px;
    line-height: 60px;
    text-align: center;
    color: $color-default-black;
    margin: 0 auto 65px;
  }

  &__help {
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    text-align: start;
    width: 490px;
    margin: 0 auto 91px;
  }

  &__image {
    display: flex;
    margin: 0 auto 20px;
    justify-content: center;

    img {
      width: 157px;
      height: 184px;
    }
  }
}
