.catalog-filter {
  background-color: $color-snow;

  &__first-level {
    padding-top: 66px;
    margin-bottom: 60px;
  }

  &__second-level {
    min-height: 202px;
  }

  &__title {
    font-family: $ff-source-code-pro;
    font-size: 32px;
    font-weight: 400;
    line-height: 40px;
    letter-spacing: 0.1em;
    color: $color-accent-second;
    margin: 0;

    &--first-level {
      margin-bottom: 43px;
    }

    &--second-level {
      margin-bottom: 43px;
    }
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;

    &--first-level {
      margin-left: -50px;
      @include vp-1279 {
        margin-left: -20px;
      }

      & .custom-toggle__label {
        @include vp-1279 {
          font-size: 37px;
        }
      }
    }

    &--second-level {
      margin-left: -49px;

      @include vp-1279 {
        margin-left: -20px;
      }
    }
  }

  &__item {
    &--first-level {
      margin-left: 40px;
      margin-bottom: 20px;
      @include vp-1279 {
        margin-left: 20px;
      }
    }
  }

  &__item {
    &--second-level {
      margin-left: 49px;
      margin-bottom: 20px;

      @include vp-1279 {
        margin-left: 20px;
      }
    }
  }

}
