.random-main {
  padding-top: 63px;
  padding-bottom: 153px;

  &__title {
    font-size: 48px;
    line-height: 60px;
    font-weight: 400;
    margin: 0;
  }

  &__list {
    padding: 0;
    margin-top: 37px;
    margin-bottom: 0;
    list-style-type: none;
    grid-gap: 65px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include hover-focus {
      .random-main__subtitle {
        color: $color-accent-main;
      }
    }
  }

  &__icon-wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;

    content: "";
    display: block;
    padding-bottom: calc(242 / 238 * 100%);
  }

  &__icon {
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__link {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    outline: none;

    @include hover-focus {
      .random-main__subtitle {
        color: $color-accent-main;
      }
    }
  }


  &__subtitle {
    margin-top: 47px;
    margin-bottom: 0;
    text-align: center;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
  }
}
