.not-found {
  width: 100%;
  height: auto;
  max-width: 1280px;

  margin: 0 auto;
  margin-top: auto;

  overflow: hidden;

  &__content-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    margin-top: auto;
    padding-top: 30px;
  }

  &__wrapper {
    width: 684px;
    min-height: 423px;

    display: flex;

    position: relative;
    top: 25%;
    left: 19%;
  }

  &__container {
    height: auto;
  }

  svg {
    position: absolute;
    top: 0;
    left: 0;

    z-index: 1;

    min-height: 100%;
  }

  &__content {
    max-width: 675px;

    padding: 96px 22px 40px 63px;

    display: inline-block;

    position: relative;
    z-index: 2;

    font-size: 32px;
    line-height: 40px;
    text-align: center;
    letter-spacing: 0.1em;
  }

  &__text {
    margin: 0;

    &:first-child {
      margin-bottom: 41px;
    }
  }

  &__img-wrapper {
    position: relative;
    bottom: 0;
    left: 43%;
  }
}
