.item-details {
  padding-top: 32px;
  padding-bottom: 52px;

  &--form-open {
    background-color: $color-snow;
  }

  &__wrapper {
    padding-right: 27px;
  }

  &__top-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 36px;
  }

  &__name {
    margin: 0;
    font-weight: 400;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: 0.1em;
    max-width: 55%;
  }

  &__price {
    font-size: 32px;
    max-width: 40%;
  }

  &__weight-wrapper {
    display: flex;
    margin-bottom: 37px;
  }

  &__weight {
    font-weight: 300;
    font-size: 24px;
    text-align: right;
    margin-left: auto;
  }

  &__image-wrapper {
    position: relative;
    overflow: hidden;
    width: 401px;
    margin-bottom: auto;
    padding-bottom: 104.5%;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__label {
    position: absolute;
    max-width: 50%;
    padding: 1px 21px;
    top: 25px;
    left: 0;
    color: $color-default-white;
    background-color: $color-tomato;
    overflow: hidden;
  }

  &__bottom-wrapper {
    display: grid;
    grid-template-columns: 393px auto;
    column-gap: 138px;
  }

  &__review-wrapper {
    .star-rating {
      margin-top: 21px;
    }
  }

  &__text-wrapper {
    margin: 69px 0 131px 4px;
  }

  &__text {
    font-size: 24px;
    line-height: 30px;
  }

  &__more {
    display: inline-block;
    vertical-align: middle;
    background-color: $color-transparent;
    width: 27px;
    height: 17px;
    cursor: pointer;
    padding: 0;
    border: none;
    color: $color-accent-main;
    letter-spacing: -0.3em;
    font-size: 16px;
    transition: all $trans-default;

    @include hover-focus {
      color: $color-geraldine;
    }

    @include active {
      color: $color-brown;
    }
  }

  &__like-button {
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    outline: none;

    svg {
      color: $color-brown;
      fill: transparent;
      transition: all $trans-default;
    }

    @include hover-focus {
      svg {
        fill: $color-accent-main;
        color: $color-accent-main;
      }
    }

    @include active {
      svg {
        fill: $color-brown;
        color: $color-brown;
      }
    }

    &:disabled {
      svg {
        color: $color-gainsboro-2;
        fill: $color-gainsboro-2;
      }
    }

    &--active {
      svg {
        fill: $color-brown;
        color: $color-brown;
      }
    }
  }

  &__button-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    .btn {
      width: 280px;
      margin-right: -5px;
    }
  }
}
