.favourites {
  background-color: $color-snow;
  padding: 106px 0 100px 0;

  .catalog {
    min-height: 500px;
  }

  .catalog__wrapper {
    background-color: $color-snow;
    padding: 45px 0 100px;
  }

  .catalog__list {
    margin: 0;
  }

  &__button {
    display: flex;
    margin: 0 60px 20px auto;
    max-width: fit-content;

    button {
      max-width: 240px;
      align-self: center;
    }

    button + button {
      margin: 0 0 0 20px;
    }
  }
}
