.catalog {
  flex-grow: 1;

  &__wrapper {
    padding: 100px 0;
    background-color: $color-default-white;

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 85px 90px;
    padding: 0;

    margin: 0 0 129px;

    @include vp-1279 {
      grid-template-columns: repeat(2, 1fr);
      margin: 0 auto 129px;
    }
  }

  &__item {
    list-style: none;
  }

  &__button-wrapper {
    max-width: 280px;
    margin-right: 43px;
    margin-left: auto;

    button + button {
      margin-top: 10px;
    }
  }
}
