.back-link {
  background-color: $color-snow;
  padding: 51px 0 30px 0;

  &__link {
    font-family: $ff-source-code-pro;
    font-size: 24px;
    font-weight: 400;
    line-height: 30px;
    display: flex;
    align-items: center;
    color: $color-persian-red;
    transition: color $trans-default;
    width: fit-content;

    @include hover-focus {
      color: $color-geraldine;
    }

    @include active {
      color: $color-brown;
    }
  }

  &__icon {
    color: $color-accent-second;
    order: -1;
    margin-right: 15px;
  }
}
