.card-item {
  display: grid;
  grid-template-columns: 1fr 75px;
  grid-template-rows: repeat(4, min-content);

  @include vp-1279 {
    grid-template-rows: repeat(3, min-content);
  }

  &--big {

    & .card-item__price {
      @include vp-1279 {
        font-size: 40px;
      }
    }

    & .card-item__title {
      margin: 0;

      display: flex;
      flex-direction: column;

      font-size: 32px;
      line-height: 40px;
      letter-spacing: 0.1em;
      text-align: left;
    }

    & .card-item__img-wrapper {

      width: 328px;

      &::before {
        padding-bottom: calc(326 / 320 * 100%);
      }
    }
  }

  &__img-link {
    display: flex;

    position: relative;

    grid-area: 1 / 1 / 3 / 3;

    outline: none;

    cursor: pointer;
    transition: color $trans-default;

    @include hover-focus {
      &~.card-item__link {
        color: $color-accent-main;
      }
    }
  }

  &__img-wrapper {
    position: relative;
    overflow: hidden;

    width: 241px;

    &::before {
      content: "";
      display: block;
      padding-bottom: calc(242 / 238 * 100%);
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &__label {
    max-width: 186px;

    overflow: hidden;

    padding: 2px 21px;

    position: absolute;
    top: 24px;
    left: 0;

    z-index: 2;

    background-color: $color-light-salmon;
    color: $color-default-white;
  }

  &__favorites {
    width: 75px;
    height: 75px;

    grid-area: 2 / 2 / 3 / 3;

    padding: 0;

    display: flex;

    z-index: 3;

    border: none;
    background-color: $color-favorites-substrate;

    cursor: pointer;
    transition: background-color $trans-default;

    &:disabled {
      pointer-events: none;

      svg {
        color: $color-gainsboro;
        fill: $color-gainsboro;
      }
    }

    &--disabled {
      pointer-events: none;

      svg {
        color: $color-gainsboro;
        fill: $color-gainsboro;
      }
    }

    @include hover-focus {
      background-color: $color-favorites-substrate-hover;
      outline: none;

      svg {
        color: $color-persian-red;
        fill: $color-persian-red;
      }
    }

    @include active {
      svg {
        color: $color-brown;
        fill: $color-brown;
      }
    }

    svg {
      margin: auto;
      transition: color $trans-default, fill $trans-default;
      color: $color-brown;
      fill: transparent;
    }

    &--active {
      svg {
        color: $color-brown;
        fill: $color-brown;
      }
    }

    &--disabled {
      svg {
        color: $color-gainsboro;
        fill: $color-gainsboro;
      }
    }
  }

  &__link {
    display: inline-block;
    grid-area: 4 / 1 / 5 / 3;
    transition: color $trans-default;

    max-width: 241px;

    @include hover-focus {
      outline: none;

      & .card-item__title {
        color: $color-accent-main;
      }
    }
  }

  &__price {
    margin: 7px 0 18px 0;
    grid-area: 3 / 1 / 4 / 3;

    font-weight: 500;
    font-size: 40px;
    line-height: 50px;
  }

  &__title {
    margin: 46px 0 0;

    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    text-align: center;

    transition: color $trans-default;

    @include hover-focus {
      outline: none;
    }

    @include vp-1279 {
      letter-spacing: 0;
      font-size: 24px;
    }
  }
}
