.error-page {
  background-color: $color-snow;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;


  &__title {
    color: $color-accent-main;
    font-size: 128px;
    line-height: 161px;
    margin: 0 auto 84px;
    padding: 0;
    text-align: center;
    font-weight: 400;
  }

  &__message {
    font-size: 48px;
    line-height: 60px;
    color: $color-accent-main;
    text-align: center;
    margin: 0 0 115px;
    padding: 0;
  }

  &__text {
    margin: 0 auto 119px;
    font-size: 24px;
    line-height: 39px;
    text-align: center;
    color: $color-accent-main;
    max-width: 432px;
  }

  &__button {
    max-width: 380px;
    text-align: center;
    margin: 0 auto 20px;
  }
}
