.loader {
  position: relative;
	width: 40px;
	height: 40px;
	display: inline-block;

  &::before,
  &::after {
    content: "";
    display: block;
    position: absolute;
    border-width: 2px;
    border-style: solid;
    border-radius: 50%;
  }

  &::before {
    width: 40px;
    height: 40px;
    border-bottom-color: $color-brown;
    border-right-color: $color-brown;
    border-top-color: $color-brown;
    border-left-color: transparent;
    animation: loader-animation 1.5s linear infinite;
  }

  &::after {
    width: 30px;
    height: 30px;
    border-bottom-color: $color-geraldine;
    border-right-color: transparent;
    border-top-color: $color-geraldine;
    border-left-color: $color-geraldine;
    top: 5px;
    left: 5px;
    animation: loader-animation-2 1s linear infinite;
  }

  @keyframes loader-animation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(-360deg);
    }
  }
  @keyframes loader-animation-2 {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

}
