.empty-favorites {
  width: 100%;

  padding-top: 20px;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex-grow: 1;

  background:
      url("../img/svg/bg-paw.svg") no-repeat top 1% right 22%,
      url("../img/svg/bg-paw.svg") no-repeat top 9% right 32%;

  background-color: $color-snow;

  overflow: hidden;
  position: relative;

  &__bg-wrapper {
    margin-top: auto;

    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  &__wrapper {
    width: 914px;
    min-height: 707px;

    display: flex;

    position: relative;

    top: 171px;
    left: 116px;
  }

  svg {
    position: absolute;
    top: 0;
    left: 0;

    z-index: 1;

    min-height: 100%;
  }

  &__content {
    max-width: 920px;

    padding: 142px 49px 315px 58px;

    display: inline-block;

    position: relative;
    z-index: 2;
  }

  &__text {
    margin: 0 0 73px 0;

    font-size: 48px;
    line-height: 60px;
    text-align: center;
  }

  &__button-wrapper {
    margin: 0 auto 0 33%;

    max-width: 300px;
  }

  &__img-wrapper {
    position: relative;
    left: 60px;
    align-self: flex-end;

    img {
      position: relative;
      right: 0;
      bottom: -161px;
    }
  }
}
