.hero {
  width: 100%;
  position: relative;

  .container {
    position: relative;
  }

  @include retina {
    background: url("../img/content/bg-hero@2x.png") repeat-x,
      $color-snow;
  }

  background: url("../img/content/bg-hero.png") repeat-x,
  $color-snow;

  &__wrapper {
    padding: 137px 0 61px 0;

    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 2;
  }

  &__subtitle {
    margin: 0;

    font-size: 32px;
    line-height: 40px;
    letter-spacing: 0.1em;
  }

  &__title {
    margin: 0 0 11px -12px;

    font-style: normal;
    font-weight: 800;
    font-size: 168px;
    line-height: 211px;
    letter-spacing: 0.2em;

    text-transform: uppercase;
  }

  &__button-wrapper {
    max-width: 344px;

    margin: 0 auto;
    width: 100%;
    padding-left: 32px;
    padding-right: 32px;

    text-align: center;
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: 0;
  }

  &__img-wrapper {
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    overflow: hidden;
    z-index: 0;
  }
}
