.review-form {
  padding-top: 60px;
  padding-bottom: 47px;

  &__wrapper {
    padding: 0 103px;
  }

  .input-star-rating {
    margin-top: 14px;
    margin-bottom: 10px;
  }

  &__title {
    font-weight: 400;
    font-size: 32px;
    line-height: 40px;
    margin: 0;
    letter-spacing: 0.1em;
  }

  &__form {
    margin-top: 79px;
  }

  .custom-input {
    &:not(:last-of-type) {
      margin-bottom: 101px;
    }
  }

  &__submit-wrapper {
    display: flex;
    flex-wrap: wrap;
    margin-top: 99px;
    justify-content: space-between;

    .input-star-rating {
      margin-right: 50px;
    }

    @include vp-1023 {
      flex-direction: column;
       justify-content: flex-start;
       align-items: flex-start;
      }
  }

  &__rating-wrapper,
  &__button-wrapper {
    display: flex;
    flex-direction: column;
    max-width: 45%;
  }

  &__button {
    width: 340px;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-bottom: 10px;
  }
}
