.input-star-rating {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  position: relative;

  input[type="radio"] {
    position: absolute;

    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;

    white-space: nowrap;

    border: 0;

    clip: rect(0 0 0 0);
    clip-path: inset(100%);

    &:checked ~ label svg {
      fill: $color-kournikova;
    }
  }

  label {
    cursor: pointer;
    display: flex;

    &:not(:first-of-type) {
      margin-right: 22px;
    }

    @include hover-focus {
      svg,
      ~ label svg {
        fill: $color-kournikova;
      }
    }
  }

  svg {
    stroke: $color-kournikova;
    stroke-width: 2px;
    fill: $color-default-white;
    transition: fill $trans-default;
  }
}
