.custom-input {
  position: relative;
  display: flex;
  padding: 0;
  width: 100%;

  label {
    display: flex;
    align-items: center;
    width: 100%;

    input {
      flex-grow: 1;
      padding: 25px 0 10px 0;
      min-height: 61px;
      transition: border-bottom-color $trans-default;
      border-top: none;
      border-right: none;
      border-bottom: 2px solid $color-geraldine;
      border-left: none;
      outline: none;
      font-family: $ff-source-code-pro;
      font-size: 18px;
      font-weight: 400;
      line-height: 23px;
      text-align: left;


      &::placeholder {
        color: $color-light-salmon;
      }

      &[type="file"] {
        color: transparent;
        cursor: pointer;

        &::before {
          content: attr(data-text);
          margin-right: 10px;
          color: $color-light-salmon;
        }
      }


      &::file-selector-button {
        display: block;
        position: absolute;
        right: 0;
        bottom: 11px;
        padding: 0;
        margin: 0;
        outline: none;
        border: none;
        color: transparent;
        background-color: transparent;
        background-image: url("../img/svg/icon-file-load.svg");
        width: 36px;
        height: 36px;
        cursor: pointer;
        transition: background-color $trans-default;
      }

      &:hover {
        border-bottom: 2px solid $color-accent-main;

        &::file-selector-button {
          background-image: url("../img/svg/icon-file-load-hover.svg");
        }
      }

      &:focus {
        border-bottom: 2px solid $color-accent-main;

        &::file-selector-button {
          background-color: $color-accent-main;
        }
      }
    }
  }

  &__label {
    position: absolute;
    padding: 0;
    margin: -1px;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    white-space: nowrap;
    border: 0;
    clip-path: inset(100%);
  }

  &.is-invalid {
    input {
      border-bottom-color: $color-brown;

      &:focus,
      &:hover {
        border-bottom-color: $color-accent-main;
      }
    }
  }

  &.is-valid {
    input {
      border-bottom-color: $color-fern-green;

      &:focus,
      &:hover {
        border-bottom-color: $color-accent-main;
      }
    }
  }

  &__message {
    position: absolute;
    top: 0;
    right: 0;
    font-family: $ff-source-code-pro;
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    color: $color-light-salmon;
  }

  &.is-invalid {
    & .custom-input__message {
      color: $color-brown;
    }
  }

  &.is-valid {
    border-bottom-color: #15ab07;
  }

  &.file-selected {
    & label input[type="file"] {
      color: black;

      &::before {
        content: none;
      }

      &::file-selector-button {
        display: none;
      }

    }
  }
}
