.empty-results {
  padding: 100px 0;
  flex-grow: 1;
  background-color: $color-snow;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    font-weight: 400;
    font-size: 32px;
    line-height: 40px;
    text-align: center;
    padding: 0 110px;
    letter-spacing: 0.1em;
    margin-top: 0;
    margin-bottom: 70px;
  }

  &--has-sort {
    .empty-results__title {
      margin-bottom: 70px;
    }

    .empty-results__button {
      width: 280px;
      margin-bottom: 70px;
    }
  }
}
