.review {
  padding: 75px 40px 25px 40px;

  &__inner-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    background-color: $color-default-white;
    padding: 27px 281px 65px 198px;
    border-radius: 10px;
    min-height: 309px;

    &::before,
    &::after {
      content: "";
      position: absolute;
      display: block;
      top: 78px;
      left: 108px;
      width: 68px;
      height: 48px;
      background-image: url("../img/stack/icon-quotes.svg");
      background-repeat: no-repeat;
    }

    &::after {
      top: unset;
      left: unset;
      transform: rotate(180deg);
      right: 175px;
      bottom: 22px;
    }

    &--border {
      border: 2px solid $color-accent-main;
    }

    .star-rating {
      align-self: flex-end;
      margin-bottom: 25px;
      margin-right: 16px;
    }
  }

  &__image-wrapper {
    position: absolute;
    width: 162px;
    height: 162px;
    top: -69px;
    right: 65px;

    img {
      border-radius: 50%;
      width: 162px;
      height: 162px;
    }
  }

  &__date {
    position: absolute;
    top: -22px;
    left: 67px;
    margin: 0;
    padding: 17px 22px;
    font-size: 24px;
    line-height: 10px;
    background-color: $color-pippin;
  }

  &__author {
    font-size: 24px;
    line-height: 30px;
    color: $color-accent-main;
    text-align: right;
    align-self: flex-end;
    margin-bottom: 28px;
    margin-right: 14px;
  }

  &__text {
    text-align: justify;
    font-size: 16px;
    line-height: 20px;
    margin: 0;

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}
