.header {
  background-color: $color-transparent;
  padding: 42px 0 36px 0;

  &--authorized {
    .header__user-info-wrap {
      order: -1;
    }

    .header__buttons {
      gap: 73px;
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: self-start;
  }

  &__logo {
    min-width: 170px;
    min-height: 69px;
    margin: -7px 60px 0 0;

    img {
      object-fit: cover;
    }
  }

  &__buttons {
    display: grid;
    gap: 27px;
    grid-template-columns: min-content 1fr;
    align-items: center;
    margin: 0 10px 0 0;
  }

  &__buttons-authorized {
    display: grid;
    grid-template-columns: 1fr;
    gap: 10px;
  }

  &__btn {
    min-width: 150px;
    max-width: min-content;
    text-align: center;
  }

  &__link--reg {
    padding: 12px 20px;
  }

  &__user-info {
    display: grid;
    align-items: center;
    gap: 27px;
    grid-template-columns: 62px 1fr;
    margin: 0 20px 0 0;

    &:not(:last-child) {
      margin: 0 20px 20px 0;
    }
  }

  &__user-avatar {
    border-radius: 50%;
    margin: 0;
    width: 62px;
    height: 62px;

    img {
      border-radius: 50%;
      object-fit: cover;
      object-position: center;
      width: 100%;
      height: 100%;
    }
  }

  &__user-mail {
    color: $color-accent-main;
    font-size: 24px;
    line-height: 30px;
    padding: 0;
    margin: 0;
  }

  &__favourite {
    position: relative;
    align-self: self-start;
    margin: 15px 0 0;
  }

  &__favourite-icon {
    display: inline-block;
    width: 33px;
    height: 29px;
    background-repeat: no-repeat;

    svg {
      fill: $color-accent-main;
      color: $color-accent-main;
    }
  }

  &__favourite-number {
    position: absolute;
    right: -15px;
    top: -6px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: max-content;
    min-width: 20px;
    height: 20px;
    font-size: 16px;
    line-height: 29px;
    color: $color-default-black;
    border-radius: 10px;
    padding-left: 3px;
    padding-right: 3px;
    background-color: $color-kournikova;
  }
}
