.error-comments {
  padding: 100px 0;
  flex-grow: 1;
  background-color: $color-snow;

  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    text-align: center;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: 0.1em;
    font-weight: 400;
    margin-top: 0;
    margin-bottom: 124px;
  }

  &__button {
    width: 310px;
    padding-top: 21px;
    padding-bottom: 21px;

    &:not(:last-of-type) {
      margin-bottom: 15px;
    }
  }
}
