.comments {
  padding-top: 12px;
  padding-bottom: 169px;
  background-color: $color-snow;

  &__show-more {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  &__button {
    width: 230px;
    margin-top: 38px;

    &:not(:last-of-type) {
      margin-right: 15px;
    }
  }

  .review {
    &:not(:last-child) {
      margin-bottom: 86px;
    }
  }
}
