.custom-toggle {
  position: relative;
  display: flex;

  input {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    clip: rect(0 0 0 0);
  }

  label {
    position: relative;
    padding-left: 52px;
    cursor: pointer;
  }

  &--radio {
    label {
      font-family: $ff-source-code-pro;
      font-size: 48px;
      font-weight: 400;
      line-height: 60px;
      color: $color-accent-second;
      transition: color $trans-default;

      &::before {
        content: "";
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
        display: block;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background-color: $color-accent-main;
        opacity: 0;
        transition: opacity $trans-default;
      }
    }

    input:hover + label,
    input:focus-visible + label {
      color: $color-light-salmon;
    }

    input:checked + label {
      color: $color-accent-main;

      &::before {
        opacity: 1;
      }
    }
  }

  &--checkbox {
    label {
      font-family: $ff-source-code-pro;
      font-size: 24px;
      font-weight: 400;
      line-height: 10px;
      color: $color-accent-second;
      background-color: $color-default-white;
      border-radius: 25px;
      padding: 24px 30px;
      transition: color $trans-default, background-color $trans-default;
    }

    input:hover + label,
    input:focus-visible + label {
      background-color: $color-light-salmon;
      color: $color-default-white;
    }

    input:checked + label {
      background-color: $color-brown;
      color: $color-default-white;
    }
  }

  &--address {
    width: 100%;
    display: flex;
    flex-direction: column;

    label {
      position: static;
      font-family: $ff-source-code-pro;
      font-size: 24px;
      font-weight: 400;
      line-height: 30px;
      padding-left: 0;
      margin-bottom: 18px;

      &::after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        display: block;
      }
    }

    label::before {
      content: "";
      position: absolute;
      right: 0;
      top: 14px;
      left: unset;
      display: block;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      background-color: $color-accent-main;
      opacity: 0;
      transition: opacity $trans-default;
    }

    .custom-toggle__icon {
      opacity: 0;
      transition: opacity $trans-default;
      order: -1;
      margin-right: -25px;
      margin-top: 4px;
    }

    .custom-toggle__address {
      padding-left: 0;
      transition: padding-left $trans-default;
      font-family: $ff-source-code-pro;
      font-size: 24px;
      font-weight: 300;
      line-height: 30px;
      display: flex;
      font-style: normal;
    }

    input:checked ~ .custom-toggle__address {
      .custom-toggle__icon {
        opacity: 1;
        margin-right: 37px;
        margin-left: -27px;
      }
    }

    input:checked ~ .custom-toggle__address {
      padding-left: 30px;
    }

    input:checked ~ .custom-toggle__icon {
      margin-right: 16px;
    }
  }

  &--sorting {
    width: 100%;

    label {
      width: 100%;
      font-family: $ff-source-code-pro;
      font-size: 24px;
      font-weight: 400;
      line-height: 30px;
      color: $color-accent-second;
      background-color: $color-default-white;
      border-radius: 25px;
      padding: 14px;
      transition: color $trans-default, background-color $trans-default;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      text-align: center;
    }

    input:hover + label,
    input:focus-visible + label {
      background-color: $color-light-salmon;
      color: $color-default-white;
    }

    input:checked + label {
      background-color: $color-brown;
      color: $color-default-white;
    }
  }
}
