html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Source Code Pro", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  color: #000000;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

a, label, button {
  -webkit-tap-highlight-color: transparent;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1279px) {
  .container {
    padding: 0 30px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.sitemap {
  color: #000000;
  padding-bottom: 50px;
  padding-top: 50px;
}
.sitemap h1 {
  color: #d94236;
  font-size: 22px;
  line-height: 30px;
}
.sitemap ol,
.sitemap ul {
  counter-reset: page;
  list-style: none;
  margin: 0;
  padding: 0;
}
.sitemap ul {
  counter-reset: inner-page;
  overflow: hidden;
}
.sitemap li {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
}
.sitemap li:not(:last-child) {
  margin: 0 0 10px 0;
}
.sitemap li::before {
  content: counter(page) ". ";
  counter-increment: page;
  font-size: 16px;
  line-height: 24px;
  margin: 0 5px 0 0;
}
.sitemap div {
  width: 100%;
}
.sitemap input + div {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;
  padding: 0 0 0 20px;
  transition: grid-template-rows 0.3s ease, padding 0.3s ease;
}
.sitemap input:checked + div {
  grid-template-rows: 1fr;
  padding: 10px 0 0 20px;
}
.sitemap ul > li {
  overflow: hidden;
}
.sitemap ul > li::before {
  content: counter(inner-page) ". ";
  counter-increment: inner-page;
  font-size: 16px;
  line-height: 24px;
  margin: 0 5px 0 0;
}
.sitemap a {
  color: inherit;
  display: flex;
  font-size: 16px;
  line-height: 24px;
  position: relative;
}
.sitemap a::before {
  background-color: #d94236;
  bottom: 0;
  content: "";
  height: 2px;
  left: 0;
  opacity: 0;
  position: absolute;
  transition: opacity 0.3s ease;
  width: 100%;
}
@media (hover: hover) {
  .sitemap a:hover:not(.focus-visible)::before {
    opacity: 1;
  }
}
.sitemap a.focus-visible:focus::before {
  opacity: 1;
}
.sitemap label {
  align-items: center;
  cursor: pointer;
  display: flex;
  font-size: 16px;
  line-height: 24px;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .sitemap label:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.sitemap label.focus-visible:focus {
  opacity: 0.6;
}
.sitemap label::after {
  border: 1px solid #d94236;
  border-left: none;
  border-top: none;
  content: "";
  display: inline-block;
  flex-shrink: 0;
  height: 10px;
  margin: -5px 0 0 20px;
  transform: rotate(45deg);
  width: 10px;
}

.uikit {
  padding: 50px 0;
  scroll-behavior: smooth;
}
@media (max-width: 1023px) {
  .uikit {
    padding: 50px 0 100px;
  }
}
.uikit__header-link {
  display: inline-flex;
}
.uikit__text {
  font-size: 14px;
  line-height: 18px;
  margin: 0 0 10px 0;
}
.uikit__text--class {
  font-family: monospace, monospace;
  font-size: 16px;
  line-height: 22px;
}
.uikit h1 {
  font-size: 30px;
  line-height: 36px;
  margin: 30px 0 30px;
  color: #d94236;
}
@media (max-width: 767px) {
  .uikit h1 {
    font-size: 24px;
    line-height: 30px;
  }
}
.uikit h2 {
  font-size: 20px;
  line-height: 26px;
  position: relative;
  margin: 0;
  padding: 5px 0;
  color: #000000;
}
.uikit section > h2 {
  margin: 0 0 15px 0;
  padding: 50px 0 0 0;
}
@media (max-width: 767px) {
  .uikit section > h2 {
    padding: 30px 0 0;
  }
}
.uikit__component {
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding: 20px;
}
@media (max-width: 767px) {
  .uikit__component {
    padding: 20px 0;
  }
}
.uikit__component::before {
  position: absolute;
  top: 50%;
  left: 0;
  width: 1px;
  height: calc(100% - 40px);
  content: "";
  transform: translateY(-50%);
  background-color: #d94236;
}
@media (max-width: 767px) {
  .uikit__component::before {
    left: -10px;
  }
}
.uikit__component pre {
  width: 100%;
  margin: 15px 0 0 0;
}
.uikit__sections {
  position: fixed;
  z-index: 1000;
  right: 0;
  top: 0;
  overflow-y: scroll;
  width: min-content;
  max-width: 30%;
  max-height: calc(100vh - 50px);
  padding: 15px;
  counter-reset: sections;
  color: #ffffff;
  border-radius: 0 0 10px 10px;
  background-color: #d94236;
}
@media (max-width: 1023px) {
  .uikit__sections {
    width: 100%;
    max-width: 100%;
  }
}
.uikit__sections::-webkit-scrollbar {
  display: none;
}
.uikit__sections label {
  font-size: 16px;
  font-weight: 600;
  line-height: 22px;
  display: flex;
  align-items: center;
  margin: 0;
  cursor: pointer;
  transition: opacity 0.3s ease;
  color: inherit;
}
@media (max-width: 1023px) {
  .uikit__sections label {
    justify-content: center;
  }
}
@media (hover: hover) {
  .uikit__sections label:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.uikit__sections label.focus-visible:focus {
  opacity: 0.6;
}
.uikit__sections label::after {
  display: inline-block;
  flex-shrink: 0;
  width: 10px;
  height: 10px;
  margin: -5px 0 0 20px;
  content: "";
  transform: rotate(45deg);
  border: 1px solid #ffffff;
  border-top: none;
  border-left: none;
}
.uikit__sections div {
  display: grid;
  padding: 0;
  transition: grid-template-rows 0.3s ease, padding 0.3s ease;
  grid-template-columns: 1fr;
  grid-template-rows: 0fr;
}
.uikit__sections input:checked + div {
  padding: 10px 0 0 0;
  grid-template-rows: 1fr;
}
.uikit__sections ul {
  overflow: hidden;
  margin: 0;
  padding: 0;
  list-style: none;
}
.uikit__sections li::before {
  font-size: 14px;
  line-height: 18px;
  margin: 0 5px 0 0;
  content: counter(sections) ". ";
  counter-increment: sections;
}
.uikit__sections a {
  font-size: 14px;
  line-height: 18px;
  position: relative;
  color: inherit;
}
.uikit__sections a::before {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  content: "";
  transition: opacity 0.3s ease;
  opacity: 0;
  background-color: #000000;
}
@media (hover: hover) {
  .uikit__sections a:hover:not(.focus-visible)::before {
    opacity: 0.8;
  }
}
.uikit__sections a.focus-visible:focus::before {
  opacity: 0.8;
}

.header {
  background-color: rgba(255, 255, 255, 0);
  padding: 42px 0 36px 0;
}
.header--authorized .header__user-info-wrap {
  order: -1;
}
.header--authorized .header__buttons {
  gap: 73px;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: self-start;
}
.header__logo {
  min-width: 170px;
  min-height: 69px;
  margin: -7px 60px 0 0;
}
.header__logo img {
  object-fit: cover;
}
.header__buttons {
  display: grid;
  gap: 27px;
  grid-template-columns: min-content 1fr;
  align-items: center;
  margin: 0 10px 0 0;
}
.header__buttons-authorized {
  display: grid;
  grid-template-columns: 1fr;
  gap: 10px;
}
.header__btn {
  min-width: 150px;
  max-width: min-content;
  text-align: center;
}
.header__link--reg {
  padding: 12px 20px;
}
.header__user-info {
  display: grid;
  align-items: center;
  gap: 27px;
  grid-template-columns: 62px 1fr;
  margin: 0 20px 0 0;
}
.header__user-info:not(:last-child) {
  margin: 0 20px 20px 0;
}
.header__user-avatar {
  border-radius: 50%;
  margin: 0;
  width: 62px;
  height: 62px;
}
.header__user-avatar img {
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}
.header__user-mail {
  color: #d94236;
  font-size: 24px;
  line-height: 30px;
  padding: 0;
  margin: 0;
}
.header__favourite {
  position: relative;
  align-self: self-start;
  margin: 15px 0 0;
}
.header__favourite-icon {
  display: inline-block;
  width: 33px;
  height: 29px;
  background-repeat: no-repeat;
}
.header__favourite-icon svg {
  fill: #d94236;
  color: #d94236;
}
.header__favourite-number {
  position: absolute;
  right: -15px;
  top: -6px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  min-width: 20px;
  height: 20px;
