.loader {
  position: relative;
  width: 40px;
  height: 40px;
  display: inline-block;
}
.loader::before, .loader::after {
  content: "";
  display: block;
  position: absolute;
  border-width: 2px;
  border-style: solid;
  border-radius: 50%;
}
.loader::before {
  width: 40px;
  height: 40px;
  border-bottom-color: #AA3128;
  border-right-color: #AA3128;
  border-top-color: #AA3128;
  border-left-color: transparent;
  animation: loader-animation 1.5s linear infinite;
}
.loader::after {
  width: 30px;
  height: 30px;
  border-bottom-color: #DD7870;
  border-right-color: transparent;
  border-top-color: #DD7870;
  border-left-color: #DD7870;
  top: 5px;
  left: 5px;
  animation: loader-animation-2 1s linear infinite;
}
@keyframes loader-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}
@keyframes loader-animation-2 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

