.btn {
  display: inline-flex;
  padding: 13px 13px;

  color: $color-default-white;

  background-color: $color-accent-main;
  border: none;
  cursor: pointer;
  border-radius: 25px;
  justify-content: center;
  font-family: $ff-source-code-pro;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  letter-spacing: 0.00em;
  user-select: none;

  transition: background-color $trans-default;
  width: 100%;

  &:disabled,
  &.is-disabled {
    background-color: $color-gainsboro;
    pointer-events: none;
  }

  @include hover-focus {
    background-color: $color-light-salmon;
    outline: none;
  }

  @include active {
    background-color: $color-brown;
  }

  &--large {
    padding: 28px 154px;
  }

  &--second {
    color: $color-brown;
    background-color: $color-transparent;

    @include hover-focus {
      background-color: $color-light-salmon;
      color: $color-brown;
    }

    @include active {
      background-color: $color-geraldine;
      color: $color-brown;
    }

    &:disabled,
    &.is-disabled {
      color: $color-default-white;
    }
  }

  &--third {
    color: $color-brown;
    border: 1px solid $color-brown;
    padding: 12px 13px;
    background-color: $color-transparent;

    @include hover-focus {
      background-color: $color-light-salmon;
      color: $color-brown;
    }

    @include active {
      background-color: $color-geraldine;
      color: $color-brown;
    }

    &:disabled,
    &.is-disabled{
      border-color: $color-pink-swan;
      color: $color-default-white;
    }
  }

  &--filter-first-level {
      position: relative;
      font-family: $ff-source-code-pro;
      font-size: 48px;
      font-weight: 400;
      line-height: 60px;
      color: $color-accent-second;
      transition: color $trans-default;
      background-color: $color-transparent;
      padding: 0 0 0 55px;
      border-radius: 0;

      &::before {
        content: "";
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
        display: block;
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background-color: $color-accent-main;
        opacity: 0;
        transition: opacity $trans-default;
      }

    @include hover-focus {
      color: $color-light-salmon;
      background-color: $color-transparent;
    }

    &.is-active {
      color: $color-accent-main;
      background-color: $color-transparent;

      &::before {
        opacity: 1;
      }
    }
  }
}
